import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			leftTopText: '年龄',
			themeColor: '#a69eff',
			age: '',
			active: false, 
			selectList: [{
				label: '22',
				value: '22'
			}, { 
				label: '23', 
				value: '23'
			}, {
				label: '24',
				value: '24'
			}],
			gender: ['男性', '女性', '其他'],
			isInfect: ['有', '无'],
			bladder: ['正常或高', '低'],
			course: ['<=5年', '>5年'],
			incontinence: ['有', '无'],
			genderIndex: -1,
			isInfectIndex: -1,
			bladderIndex: -1,
			courseIndex: -1,
			incontinenceIndex: -1,
			imgList: [],
			isHide: false
		}
	},
	methods: {
		open() {
			this.$refs.selector.show();
		},
		onConfirmAge(e) {
			this.age = e.result;
		},
		next() {
			this.active = false;
		},
		selectGender(e) {
			this.genderIndex = e;
		},
		selectInfect(e) {
			this.isInfectIndex = e;
		},
		selectBladder(e) {
			this.bladderIndex = e;
		},
		selectCourse(e) {
			this.courseIndex = e;
		},
		selectIncontinence(e) {
			this.incontinenceIndex = e;
		},
		previewImg(e) {
			let current = e.target.dataset.src;
			uni.previewImage({
				current: current,
				urls: this.imgList
			})
		},
		deleteImg(index) {
			this.imgList.splice(index, 1);
		},
		addImg() {
			uni.chooseImage({
				count: 3 - this.imgList.length,
				success: res => {
					this.imgList.push(res.tempFilePaths[0])
				},
				fail: err => {
					console.log(err);
				}
			})
		},
		confirm() {
			this.isHide = true;
		},
		reback(){
			uni.navigateBack({
				delta: 1
			})
		}
	},
	components: {
		wPicker
	}
}
